.i {
    display: flex;
    height: 100vh;
}
.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-right{
    flex: 1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-intro{
    font-size: 30px;
    font-weight: 300;
}
.i-name{
    font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    animation: move 4s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform:translateY(-50px);
    }
    50%{
        transform:translateY(-100);
    }
    75%{
        transform:translateY(-150px);
    }
    100%{
        transform:translateY(-200px);
    }
    
}
.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #4700D8;
    display:flex;
    align-items: center;
    
}
.i-scroll{
    width: 30%;
    height: 30px;
    position: absolute;
    bottom: 20px;
}
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}
.i-bg{
    clip-path: polygon(100% 0%, 100% 56%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #4700D8;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;

}

@media screen and (max-width: 480px){
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
    .i-title-item
    {
        justify-content: center;
    }

}